import React, { useState } from "react";
import { motion, useAnimationControls } from "framer-motion";

interface FAQData {
  title: string;
  content: string[];
}

const FAQItem = (props: { title: string; content: string[] }) => {
  const [isOpen, setIsOpen] = useState(false);
  const contentAnimation = useAnimationControls();
  const panelAnimation = useAnimationControls();

  return (
    <motion.div
      animate={panelAnimation}
      className="flex flex-col gap-3 px-6 rounded-xl cursor-pointer select-none text-justify overflow-hidden"
      initial={{
        background: "rgb(226, 232, 240)",
        width: 900,
        height: 60,
      }}
      whileHover={{ background: "rgb(203, 213, 225)", scale: 1.02 }}
      onTap={() => {
        setIsOpen(!isOpen);

        panelAnimation.start({
          height: isOpen ? 60 : "auto",
          transition: { ease: "circOut", duration: 0.2 },
        });
        contentAnimation.start({
          opacity: isOpen ? 0 : 1,
          transition: {
            duration: 0.2,
          },
        });
      }}
    >
      <div className="font-medium text-lg mt-4">Q. {props.title}</div>
      <motion.div
        animate={contentAnimation}
        initial={{ opacity: 0 }}
        className="flex relative"
      >
        <div className="flex mb-4 flex-col gap-2 px-8">
          {props.content?.map((content) => (
            <div className="flex text-sm">{content}</div>
          ))}
        </div>
      </motion.div>
    </motion.div>
  );
};

const FAQPanel = () => {};

function FAQ(props: FAQData) {
  return <FAQItem title={props.title} content={props.content} />;
}

export default FAQ;
