import React from "react";
import { AnimationControls, motion, useAnimation } from "framer-motion";

interface ActivityData {
  title: string;
  description: string[];
  image: string;
  activity: string[];
  reverseView?: boolean;
}

const Title = (props: { title: string; animate: AnimationControls }) => {
  return (
    <motion.div
      className="text-3xl font-bold"
      initial={{ opacity: 0 }}
      animate={props.animate}
    >
      {props.title}
    </motion.div>
  );
};

const Description = (props: {
  description: string[];
  animate: AnimationControls;
}) => {
  return (
    <motion.div
      className="flex flex-col gap-1"
      initial={{ opacity: 0 }}
      animate={props.animate}
    >
      {props.description?.map((data) => {
        return <div>{data}</div>;
      })}
    </motion.div>
  );
};

const Detail = (props: { activity: string[]; animate: AnimationControls }) => {
  return (
    <motion.div
      className="flex"
      initial={{ opacity: 0 }}
      animate={props.animate}
    >
      <div className="flex w-[800px] flex-wrap gap-2">
        {props.activity?.map((data) => {
          return (
            <div className="flex p-2 bg-slate-300 text-slate-600 rounded-lg text-sm">
              # {data}
            </div>
          );
        })}
      </div>
    </motion.div>
  );
};

const PrimaryImage = (props: { image: string; animate: AnimationControls }) => {
  return (
    <motion.img
      animate={props.animate}
      className="relative flex rounded-3xl shadow-roundXL object-cover"
      initial={{ opacity: 0, width: 0, height: 0 }}
      src={props.image}
      viewport={{ once: true, amount: 0, margin: "0px 0px 100px 0px" }}
    />
  );
};

function Activity(props: ActivityData) {
  const titleAnimation = useAnimation();
  const descriptionAnimation = useAnimation();
  const detailAnimation = useAnimation();
  const imageAnimation = useAnimation();

  return (
    <motion.div
      className={`flex w-[1300px] items-center gap-10 ${
        props.reverseView === true ? "flex-row-reverse" : ""
      }`}
      onViewportEnter={() => {
        titleAnimation.start({ opacity: 1 });
        descriptionAnimation.start({
          opacity: 1,
          transition: { delay: 0.3 },
        });
        detailAnimation.start({ opacity: 1, transition: { delay: 0.5 } });
        imageAnimation.start({
          width: 350,
          height: 500,
          opacity: 1,
          transition: { duration: 0.5, ease: "circOut" },
        });
      }}
    >
      <PrimaryImage animate={imageAnimation} image={props.image} />
      <div className={`flex flex-col gap-5`}>
        <Title animate={titleAnimation} title={props.title} />
        <div className="flex flex-col ml-8 gap-5">
          <Description
            animate={descriptionAnimation}
            description={props.description}
          />
          <Detail animate={detailAnimation} activity={props.activity} />
        </div>
      </div>
    </motion.div>
  );
}

export default Activity;
