import { Link } from "gatsby";
import React from "react";
import Instagram from "../../../images/logo/instagram.png";
import Line from "../../../images/logo/line.png";

interface ContactData {
  email: string | undefined;
  entry: string[] | undefined;
  description: string | undefined;
  instagram: string | undefined;
  line: string | undefined;
}

function Contact(props: ContactData) {
  return (
    <div className="flex flex-col w-fit gap-4 justify-center p-5 bg-slate-200 rounded-3xl">
      <div className="text-sm">{props.description}</div>
      <div className="flex flex-col gap-3 rounded-xl bg-white text-sm px-7 py-4">
        {props.entry?.map((entry, index) => {
          return (
            <div className="flex">
              <b>{index + 1}.</b> {entry}
            </div>
          );
        })}
      </div>
      <div className="flex items-center justify-between px-1">
        <div className="flex">
          <Link to={props.instagram || ""} target="_blank">
            <img
              className={`w-8 ${props.instagram === "" ? "collapse" : ""}`}
              src={Instagram}
            />
          </Link>

          <Link to={props.line || ""} target="_blank">
            <img
              className={`w-8 ${props.line === "" ? "collapse" : ""}`}
              src={Line}
            />
          </Link>
        </div>

        <div className="flex gap-1 text-sm items-center">
          <div className="select-none">to.</div>
          {props.email}
        </div>
      </div>
    </div>
  );
}

export default Contact;
