import React, { useEffect, useState } from "react";
import CharacterCard from "../components/Home/CharacterCard";
import Introduce from "../components/Home/Introduce";
import Contact from "../components/Home/Contact";
import Activity from "../components/Home/Activity";
import FAQ from "../components/Home/FAQ";
import { motion, useAnimation } from "framer-motion";
import { Link } from "gatsby";
import ContactData from "../../../../../data/tus-volunteer/Contact.json";
import ActivityData from "../../../../../data/tus-volunteer/Activity.json";
import ConfigData from "../../../../../data/tus-volunteer/Config.json";
import FAQData from "../../../../../data/tus-volunteer/FAQ.json";

interface CharacterCardContent {
  color: string;
  title: string;
  icon: string;
  fontSize: string;
  content: JSX.Element;
}

interface ActivityContent {
  title: string;
  description: string[];
  image: string;
  activity: string[];
  reverseView?: boolean;
}

interface FAQContent {
  title: string;
  content: string[];
}

interface ConfigContent {
  mainGroupURL: string;
  memberImageURL: string;
}

interface ContactContent {
  email: string;
  description: string;
  entry: string[];
  lineURL: string;
  instagramURL: string;
}

const IntroducePanel = () => {
  return <Introduce className="m-[120px]" />;
};

const CharacterCardPanel = () => {
  const contents: CharacterCardContent[] = [
    {
      color: "bg-rose-400",
      title: "専門性が高い",
      icon: "flexed-biceps",
      fontSize: "text-3xl",
      content: (
        <div className="flex flex-col">
          メンバーみんな、それぞれの大学で
          <br />
          専門分野を中心に学問を修め、
          <b className="underline decoration-[3px] decoration-rose-500 decoration-wavy underline-offset-4">
            最先端の生きた技術や知識を勉強
          </b>
          しています。
          <br />
          <br />
          ​そうして得た知識や技術、経験を
          <b className="underline decoration-[3px] decoration-rose-500 decoration-wavy underline-offset-4">
            本質を損なうことなく分かりやすく
          </b>
          噛み砕いて
          <b className="underline decoration-[3px] decoration-rose-500 decoration-wavy underline-offset-4">
            子どもたちの学びへと還元
          </b>
          します。
        </div>
      ),
    },
    {
      color: "bg-sky-400",
      title: "多様なコンテンツ",
      icon: "party-popper",
      fontSize: "text-3xl",
      content: (
        <div className="flex flex-col">
          Community Labには現在20人以上
          <br />
          メンバーがいますが、
          <br />
          それぞれに違った専門分野や特技、
          <br />
          趣味があります。
          <br />
          <br />
          多様な背景を持ち、異なる大学に通う
          <br />
          メンバーが集まっているからこそ、
          <br />
          その分
          <b className="underline decoration-sky-500 decoration-[3px] decoration-wavy underline-offset-4">
            多様な学びや経験を子どもたちに
            <br />
            提供
          </b>
          することができます。
        </div>
      ),
    },
    {
      color: "bg-emerald-400",
      title: "子ども達との距離が近い",
      icon: "family-man-woman-girl-boy",
      fontSize: "text-2xl",
      content: (
        <div className="flex flex-col">
          <p>
            メンバーは全員
            <b className="underline decoration-[3px] decoration-wavy decoration-emerald-500 underline-offset-4">
              現役大学生
            </b>
            なので、
          </p>
          <p>年齢的にも心理的にも</p>
          <br />
          <p>
            <b className="underline decoration-[3px] decoration-wavy decoration-emerald-500 underline-offset-4">
              子どもたちとの距離が近く、
              <br />
              コミュニケーションを
              <br />
              取りやすい
            </b>
            のが強みです。
          </p>
          <br />
          また年齢が近いからこそ、
          <br />
          子どもたちの
          <br />
          <p>
            <b className="underline decoration-[3px] decoration-wavy decoration-emerald-500 underline-offset-4">
              理想像(ロールモデル)になれる
            </b>
            と
          </p>
          自負しています。
        </div>
      ),
    },
    {
      color: "bg-orange-400",
      title: "主体性行動力",
      icon: "man-running-light-skin-tone",
      fontSize: "text-3xl",
      content: (
        <div className="flex flex-col">
          Community Labでは
          <br />
          活動理念に共感したメンバーが、
          <br />
          時間や熱意を捧げて
          <b className="underline decoration-orange-500 decoration-[3px] decoration-wavy underline-offset-4">
            主体的にこの
            <br />
            活動に取り組んでいます。
          </b>
          <br />
          <br />
          また社会人と比べて多くの
          <br />
          時間があるため、
          <br />
          学びのチャンスがあれば
          <br />
          <b className="underline decoration-orange-500 decoration-[3px] decoration-wavy underline-offset-4">
            積極的にアクションを
            <br />
            起こす行動力があります。
          </b>
        </div>
      ),
    },
  ];

  return (
    <div className="flex flex-col items-center gap-10">
      <div className="flex flex-row gap-2">
        <p className="text-4xl">
          <b className="underline decoration-pink-500 decoration-4 underline-offset-4 decoration-wavy">
            大学生
          </b>
          だから届けられる価値
        </p>
      </div>
      <div className={`flex flex-row justify-center gap-7`}>
        {contents.map((content) => (
          <CharacterCard
            color={content.color}
            title={content.title}
            icon={content.icon}
            fontSize={content.fontSize}
            content={content.content}
          />
        ))}
      </div>
    </div>
  );
};

const ContactPanel = () => {
  const [contactData, setContactData] = useState<ContactContent>();

  useEffect(() => {
    setContactData(ContactData);
  }, []);

  return (
    <div className="flex flex-col gap-5 items-center mb-20">
      <div className="text-4xl font-bold">ご依頼・お問い合わせはこちら</div>
      <Contact
        email={contactData?.email}
        entry={contactData?.entry}
        description={contactData?.description}
        instagram={contactData?.instagramURL}
        line={contactData?.lineURL}
      />
    </div>
  );
};

const ActivityPanel = () => {
  let [activityData, setActivityData] = useState<ActivityContent[]>();

  useEffect(() => {
    setActivityData(ActivityData.map((data) => data));
  }, []);

  return (
    <div className="flex flex-col gap-40">
      {activityData?.map((data, index) => {
        return (
          <Activity
            title={data.title}
            description={data.description}
            image={data.image}
            activity={data.activity}
            reverseView={Boolean(index % 2)}
          />
        );
      })}
    </div>
  );
};

const FAQPanel = () => {
  const [faqData, setFAQData] = useState<FAQContent[]>();
  useEffect(() => {
    setFAQData(FAQData.map((data) => data));
  }, []);

  return (
    <div className="flex flex-col gap-8 items-center">
      <div className="text-4xl font-bold">よくある質問</div>
      <div className="flex flex-col gap-3">
        {faqData?.map((faq) => {
          return <FAQ title={faq.title} content={faq.content} />;
        })}
      </div>
    </div>
  );
};

const MemberPanel = (props: {
  memberImageURL: string | undefined;
  mainGroupURL: string | undefined;
}) => {
  const memberImageAnimation = useAnimation();
  const titleAnimation = useAnimation();
  const descriptionAnimation = useAnimation();
  const linkButtonAnimation = useAnimation();

  return (
    <motion.div
      viewport={{ amount: 1 }}
      onViewportEnter={() => {
        memberImageAnimation
          .start({
            height: 450,
            transition: { duration: 0.8, ease: "circOut" },
          })
          .then(() => {
            titleAnimation.start({ opacity: 1 }).then(() => {
              descriptionAnimation.start({ opacity: 1 }).then(() => {
                linkButtonAnimation.start({ opacity: 1 });
              });
            });
          });
      }}
      className="flex flex-col items-center gap-5"
    >
      <div className="text-4xl font-bold">私たちの団体とは？</div>
      <motion.img
        animate={memberImageAnimation}
        initial={{ height: 200 }}
        className="flex rounded-3xl shadow-roundXL object-center"
        src={props.memberImageURL}
      />
      <div className="flex flex-col items-center gap-2">
        <motion.div
          className="text-2xl font-bold"
          animate={titleAnimation}
          initial={{ opacity: 0 }}
        >
          当団体について
        </motion.div>
        <motion.div
          className="flex flex-col items-center text-sm"
          animate={descriptionAnimation}
          initial={{ opacity: 0 }}
        >
          <div>
            当団体Community
            Labは、サイエンス分野だけでなく、現役大学生が子供たちに様々な分野のイベントやコンテンツを提供する教育コミュニティです。
          </div>
          <div>
            年齢や学校の枠を超え、生涯に亘って交流する仲間と「つながる場」を作ります。
          </div>
        </motion.div>
      </div>
      <Link to={props.mainGroupURL || ""} target="_blank">
        <motion.div
          animate={linkButtonAnimation}
          initial={{ opacity: 0 }}
          className={`p-3 px-4 text-xs rounded-3xl bg-slate-500 text-white select-none cursor-pointer ${
            props.mainGroupURL === "" ? "collapse" : ""
          }`}
        >
          団体HPはこちら
        </motion.div>
      </Link>
    </motion.div>
  );
};

function Home() {
  const [config, setConfigData] = useState<ConfigContent>();

  useEffect(() => {
    setConfigData(ConfigData);
  }, []);

  return (
    <div className="flex flex-col items-center gap-48">
      <IntroducePanel />
      <CharacterCardPanel />
      <ActivityPanel />
      <MemberPanel
        mainGroupURL={config?.mainGroupURL}
        memberImageURL={config?.memberImageURL}
      />
      <FAQPanel />
      <ContactPanel />
    </div>
  );
}

export default Home;
