import React, { useState } from "react";
import { Emoji, EmojiProvider } from "react-apple-emojis";
import emojiData from "react-apple-emojis/src/data.json";
import { motion } from "framer-motion";

interface CharacterCardData {
  color: string;
  title: string;
  content: JSX.Element;
  icon: string;
  fontSize?: string;
}

function CharacterCard(props: CharacterCardData) {
  let [tapped, setTapped] = useState(false);

  return (
    <motion.div
      className={`flex flex-col select-none cursor-pointer gap-2 items-center justify-center rounded-[20px] shadow-round w-[350px] h-[200px] ${props.color}`}
      whileHover={{
        scale: 1.05,
      }}
      whileTap={{
        scale: 1.02,
      }}
      onTap={() => {
        setTapped(!tapped);
      }}
      animate={{
        height: tapped ? 450 : 200,
      }}
      transition={{
        duration: 0.15,
        ease: "easeOut",
      }}
    >
      <motion.div
        className="flex flex-col gap-2 items-center"
        animate={{ opacity: tapped ? 0 : 1 }}
        transition={{ duration: 0.1 }}
      >
        <div>
          <EmojiProvider data={emojiData}>
            <Emoji name={props.icon} width={45} />
          </EmojiProvider>
        </div>
        <div className={`text-white font-bold ${props.fontSize}`}>
          {props.title}
        </div>
      </motion.div>
      <motion.div
        className={`absolute ${
          tapped ? "visible" : "collapse"
        } text-base text-white`}
        animate={{ opacity: tapped ? 1 : 0 }}
        transition={{ duration: 0.1 }}
      >
        {props.content}
      </motion.div>
    </motion.div>
  );
}

export default CharacterCard;
